import Map from 'ol/Map';
import OSM, { ATTRIBUTION } from 'ol/source/OSM';
import TileLayer from 'ol/layer/Tile';
import View from 'ol/View';
import XYZ from 'ol/source/XYZ.js';
import { getRenderPixel } from 'ol/render';
import { fromLonLat, transform } from 'ol/proj.js';
import ContextMenu from 'ol-contextmenu';
import '../scss/styles.scss'
import imgUrl from '../images/osm_logo.png'

// Import all of Bootstrap's JS
import * as bootstrap from 'bootstrap'


document.getElementById('logo').src = imgUrl;
document.getElementById('heading').innerHTML = 'Tileserver gauss.openstreetmap.de';



var modal = document.getElementById('modal');
var bootstrapModal = bootstrap.Modal.getOrCreateInstance(modal);

const osm = new TileLayer({
    source: new OSM(),
});

const defaultStyle = new TileLayer({
    source: new XYZ({
        attributions: [
            ATTRIBUTION,
        ],
        url: 'https://tile.openstreetmap.de/{z}/{x}/{y}.png',
        maxZoom: 20,
    }),
});

const map = new Map({
    layers: [osm, defaultStyle],
    target: 'map',
    view: new View({
        center: fromLonLat([10.33649, 51.006271]),
        projection: 'EPSG:3857',
        zoom: 6,
        constrainResolution: true,
    })
});













const swipe = document.getElementById('swipe');

defaultStyle.on('prerender', function (event) {
    const ctx = event.context;
    const mapSize = map.getSize();
    const width = mapSize[0] * (swipe.value / 100);
    const tl = getRenderPixel(event, [width, 0]);
    const tr = getRenderPixel(event, [mapSize[0], 0]);
    const bl = getRenderPixel(event, [width, mapSize[1]]);
    const br = getRenderPixel(event, mapSize);

    ctx.save();
    ctx.beginPath();
    ctx.moveTo(tl[0], tl[1]);
    ctx.lineTo(bl[0], bl[1]);
    ctx.lineTo(br[0], br[1]);
    ctx.lineTo(tr[0], tr[1]);
    ctx.closePath();
    ctx.clip();
});

defaultStyle.on('postrender', function (event) {
    const ctx = event.context;
    ctx.restore();
});

swipe.addEventListener('input', function () {
    map.render();
});







// https://wiki.openstreetmap.org/wiki/Slippy_map_tilenames#ECMAScript_.28JavaScript.2FActionScript.2C_etc..29
function long2tile(lon, zoom) {
    return (Math.floor((lon + 180) / 360 * Math.pow(2, zoom)));
}

function lat2tile(lat, zoom) {
    return (Math.floor((1 - Math.log(Math.tan(lat * Math.PI / 180) + 1 / Math.cos(lat * Math.PI / 180)) / Math.PI) / 2 * Math.pow(2, zoom)));
}

function gent_tile_url(suffix, zoom, coordinate) {
    var url = 'https://tile.openstreetmap.de/{z}/{x}/{y}.png';

    let lonlat = transform(coordinate, 'EPSG:3857', 'EPSG:4326')

    zoom = map.getView().getZoom();
    let x = long2tile(lonlat[0], zoom);
    let y = lat2tile(lonlat[1], zoom);

    url = url.replace("{x}", x);
    url = url.replace("{y}", y);
    url = url.replace("{z}", zoom);

    if (suffix != "") {
        url = url + '/' + suffix;
    }

    return url;
};


var do_after_fetch = function (data){
    document.getElementById('modal-body-id').innerHTML = data;
    bootstrapModal.show();
    console.log(data); 
}

var do_fetch = function (url){
    let response = fetch(url)
    .then(function(response) {
      return response.text();
    }).then(function(data) {
      do_after_fetch(data);
    });
}

var open_tile_cb = function (obj) {
    let url = gent_tile_url('', map.getView().getZoom(), obj.coordinate);
    document.getElementById('modal-title-id').innerHTML = 'Tile Image';
    document.getElementById('modal-body-id').innerHTML = '<img src="' + url + '">';
    bootstrapModal.show();
    //do_fetch(url);
};

var open_tile_info_cb = function (obj) {
    let url = gent_tile_url('status', map.getView().getZoom(), obj.coordinate);
    document.getElementById('modal-title-id').innerHTML = 'Tile Info';
    do_fetch(url);
};

var add_to_renderq = function (obj) {
    let url = gent_tile_url('dirty', map.getView().getZoom(), obj.coordinate);
    document.getElementById('modal-title-id').innerHTML = 'Tile aktualisieren';
    do_fetch(url);
};

var contextmenu = new ContextMenu({
    width: 170,
    defaultItems: false,
    items: [
        {
            text: 'open tile url',
            classname: 'open_tile_cb',
            callback: open_tile_cb,
        },
        {
            text: 'open tile info',
            classname: 'open_tile_info_cb',
            callback: open_tile_info_cb,
        },
        {
            text: 'add to render queue',
            classname: 'add_to_renderq',
            callback: add_to_renderq,
        },
    ],
});
map.addControl(contextmenu);







//todo debug??

var currZoom = map.getView().getZoom();
map.on('moveend', function (e) {
    var newZoom = map.getView().getZoom();
    if (currZoom != newZoom) {
        console.log('zoom end, new zoom: ' + newZoom);
        currZoom = newZoom;
    }
});
